
.content_home{
   font-family: 'Press Start 2P', cursive; 
}




.what_system{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.system_box{   
    margin-top: 50px;
}

.system_box_buttom {
    margin-top: 50px;
    width: 200px;
    height: 200px;
    border-radius: 10px;
    box-shadow: 2px 4px 20px 12px rgba(34, 60, 80, 0.67);
    transition: 0.5s linear;

}

.system_box_buttom a{
    display: block;
    width: 100%;
    height: 100%;
}

.system_windows{
    background-image: url(../image/logoWindows.png);
    background-position: center;
    background-size:  cover;
    background-repeat: no-repeat;    
}


.system_linux{
    background-image: url(../image/logoLinux.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}



.block_hello{
    min-width: 100%;
    min-height: 100%;
    padding-top: 200px;
    background: #555252;
    
    

    position: absolute;
    top: 0px;
    left: 0px;
    transition: opacity 1.3s, linear 1.3s;
}

.block_hello_image{
    width: 200px;
    height: 200px;
    margin: 100px auto;
    background-image: url(../image/cripto.png);
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 4px 4px 52px 0px rgba(34, 60, 80, 0.65); 
    transition: opacity 1.3s, linear 1.3s;
}

.cursor {
    position: relative;
    width: 24em;
    margin: 0 auto;
    border-right: 2px solid rgba(255, 255, 255, .75);
    font-size: 1em;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    transform: translateY(-50%);
}


.typewriter_animation {
    animation:
        typewriter 5s steps(50) 1s 1 normal both,
        blinkingCursor 500ms steps(50) infinite normal;
}

@keyframes typewriter {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}

@keyframes blinkingCursor {
    from {
        border-right-color: rgba(255, 255, 255, .75);
    }

    to {
        border-right-color: transparent;
    }
}