@keyframes rotation {
    100% {
        transform: scale(1.5)rotate(360deg);
        opacity: 0;
    }
}

.cube_conteiner{
    text-align: center;
    
}

.cube_conteiner h2 {
    margin-top: 50px;
}

.active{
    transform: scale(1.5)rotate(360deg);
    opacity: 0;
}



.cube_conteiner a{
    font-size: 2em;
    color: azure;
    text-shadow: 3px 3px 4px rgba(0, 0, 0, 1);
    font-family: 'Press Start 2P';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
}

.cube{
    margin:100px auto;
    transition: 0.5s linear; 

}

.remains{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    box-shadow: 0px -5px 48px 26px rgba(34, 60, 80, 0.87) inset;

}

.front {
    
    background: red;
     
}

.right {
    background: yellow;
   
}

.back {
    background: green;
 
}

.left {
    background: blue;
 
}

.top {
    background: white;
  
}

.top a {
    color: #000;
} 

.bottom {
    background: #000;
 
}

.bottom a {
    text-shadow: 3px 3px 4px rgba(254, 255, 254, 1);
}